@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;

}

.about{
  background: url("../../assets/images/demo-about.jpg") center center no-repeat;
  background-size: cover;
  height: 100vh;
}



.description {
  /*width: 100%;*/
  color: white;
  height: 100vh;
