.field {
  margin: 1em;

}

.field label {
  display: block;
  margin-bottom: 0.5em;
}

.field input {
  -webkit-transition: all .3s;
  background-color: white;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  color: black;
  cursor: text;
  display: inline-block;
  font-size: 16px;
  height: 28px;
  line-height: 1.5;
  margin: 0;
  overflow: visible;
  padding: 4px 7px;
  position: relative;
  -o-transition: all .3s;
  transition: all .3s;
  width: 200px;
}

.field input:hover {
  border-color: #49a9ee;
}

.field input:focus {
  border-color: #49a9ee;
  -webkit-box-shadow: 0 0 0 2px rgba(16,142,233,.2);
          box-shadow: 0 0 0 2px rgba(16,142,233,.2);
  outline: 0;
}

.field textarea {
  -webkit-transition: all .3s;
  opacity: .5;
  background-color: white;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  color: black;
  cursor: text;
  display: inline-block;
  font-size: 16px;
  height: 28px;
  height: auto;
  line-height: 1.5;
  margin: 0;
  max-width: 100%;
  overflow: visible;
  padding: 4px 7px;
  position: relative;
  -o-transition: all .3s;
  transition: all .3s;
  vertical-align: bottom;
}

.field textarea:hover {
  border-color: #49a9ee;
}

.field textarea:focus {
  -webkit-box-shadow: 0 0 0 2px rgba(16,142,233,.2);
          box-shadow: 0 0 0 2px rgba(16,142,233,.2);
  outline: 0;
}



@media handheld, screen and (max-width: 440px) {


.field {
    /*margin: 1em;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: inherit;
        flex-direction: inherit;
    padding-left: 1em;
}
}



