@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;

}

.header {
  background-color: black;
  margin: 0;
  padding: 5px;
  letter-spacing: 10px;
  color: white;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 5em;
}



/*.header h1 {
  display: flex;
  text-align: center;

  letter-spacing: 4px;
}*/

@media handheld, screen and (max-width: 750px) {

  .header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    letter-spacing: 10px;
  }

}

@media handheld, screen and (max-width: 440px) {

  .header {
    background-color: black;
    margin: 0;
    letter-spacing: 4px;
    color: white;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: start;
  }

  .header h1{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }

}



