@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;

}

.navbar {
  background-color: #434854  ;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  /*z-index: 1;*/
  height: 3em;
  padding-top: 1em;
  padding-bottom: .5em;
}

.navbar a {
  text-decoration: none;
margin: .2em 2em 2em 2em;
font-size: 2em;
color: white;
font-family: 'Raleway', sans-serif;
font-weight: 100;
-webkit-transition: color 0.3s;
-o-transition: color 0.3s;
transition: color 0.3s;
-webkit-transition: all 0.2s ease-in-out;
-o-transition: all 0.2s ease-in-out;
transition: all 0.2s ease-in-out;

  }

.navbar a:hover {
  color: #f67052;
}

.navbar a:hover {
  color: #f67052;
}

@media handheld, screen and (max-width: 750px) {

  .navbar nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  text-align: center;
  }

}

@media handheld, screen and (max-width: 440px) {

  /*.navbar nav {
  display: flex;
  width: 100%;
  scroll-behavior: smooth;
  }*/

  .navbar {
    background-color: #434854  ;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    /*z-index: 1;*/
    height: 14em;
    scroll-behavior: smooth;
    
  }

  .navbar a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  margin: .2em 2em 2em 2em;
  font-size: 2em;
  color: white;
  font-family: 'Raleway', sans-serif;
  font-weight: 100;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;

    }


}



